import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/services',
    name: 'Services',
    component: () => import('../views/Services.vue')
  },
  {
    path: '/services/new',
    name: 'NewService',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/NewService.vue')
  },
  {
    path: '/service/:VehicleId',
    name: 'service',
    component: () => import('../views/Vehicle.vue')
  },
  {
    path: '/bills/:billNumber',
    name: 'bills',
    component: () => import('../views/Bill.vue')
  },
  {
    path: '/garage',
    name: 'garage',
    component: () => import('../views/Garage/Garage.vue'),
    children: [
      {
        path: 'vehicles',
        name: 'garageVehicles',
        component: () => import('../views/Garage/Vehicles.vue')
      },
      {
        path: 'services',
        name: 'garageServices',
        component: () => import('../views/Garage/Services.vue')
      },
      {
        path: 'bills',
        name: 'garageBills',
        component: () => import('../views/Garage/Bills.vue')
      },
    ]

  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
