<template>
  <div class="row w-100 m-0">
    <div
      class="col-sm-12 offset-sm-0 col-md-4 offset-md-4 col-lg-4 offset-lg-4 m-0 p-0"
    >
      <main class="form-signin p-4">
        <form autocomplete="off" @submit.prevent="search()">
          <h2
            class="va-logo fw-bold mb-3 text-dark text-center border border-dark p-5"
          >
            Vishwakarma <span class="text-primary">Automobiles</span>
          </h2>

          <div class="mb-3">
            <label for="vehicle-input" class="form-label">Vehicle No.</label>
            <input
              v-model="vehicleNo"
              type="search"
              class="form-control form-control-lg"
              id="vehicle-input"
              placeholder="HRXXXXX"
              required
            />
          </div>
          <button class="w-100 btn btn-lg btn-primary mb-2" type="submit">
            Search
          </button>
          <button
            @click.prevent="newService()"
            class="w-100 btn btn-lg btn-outline-dark"
            type="submit"
          >
            New Service
          </button>

          <div class="garage my-4">
            <p class="text-center">My Garage</p>
            <div class="row p-2">
              <div class="col-4 p-1">
                <div
                  class="garage-door text-center p-3 bg-light border"
                  @click="$router.push('/garage/vehicles')"
                >
                  <p class="fw-bold">Vehicles</p>
                  <img class="img-fluid" src="../assets/vehicles.png" alt="" />
                </div>
              </div>
              <div class="col-4 p-1">
                <div
                  class="garage-door text-center p-3 bg-light border"
                  @click="$router.push('/garage/services')"
                >
                  <p class="fw-bold">Services</p>
                  <img class="img-fluid" src="../assets/services.png" alt="" />
                </div>
              </div>
              <div class="col-4 p-1">
                <div
                  class="garage-door text-center p-3 bg-light border"
                  @click="$router.push('/garage/bills')"
                >
                  <p class="fw-bold">Bills</p>
                  <img class="img-fluid" src="../assets/bills.png" alt="" />
                </div>
              </div>
            </div>
          </div>

          <p class="mt-5 mb-3 text-muted text-center">
            &copy; Vishwakarma Automobiles Services
          </p>
        </form>
      </main>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      vehicleNo: null,
    };
  },
  methods: {
    search() {
      this.$router.push({
        path: "/service/" + this.vehicleNo.toUpperCase(),
      });
    },
    // Redirecting to add service page
    newService() {
      if (this.vehicleNo) {
        this.$router.push({
          path: "/services/new",
          query: {
            vehicleNo: this.vehicleNo.toUpperCase(),
          },
        });
      } else {
        this.$router.push({
          path: "/services/new",
        });
      }
    },
  },
};
</script>

